html {
    background: #f5f7f8;
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: antialiased;
    padding: 20px 0;
  }
  
  .band {
    width: 90%;
    max-width: 1240px;
    margin: 0 auto;
    
    display: grid;
    
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    grid-gap: 20px;
    
  }
  
  /* Definimos para que a partir de 500px se muestren dos columnas */
  @media only screen and (min-width: 500px) {
    .band {
      grid-template-columns: 1fr 1fr;
    } 
    .item-1 h1 {
      font-size: 30px;
    }
  }

  /* Definimos para que a partir de 850px se muestren cuatro columnas */
  @media only screen and (min-width: 850px) {
    .band {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
  
  .card {
    min-height: 100%;
    background: white;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: #444;
    position: relative;
    top: 0;
    transition: all .1s ease-in;
  }
  
  .card:hover {
    top: -2px;
    box-shadow: 0 4px 5px rgba(0,0,0,0.2);
  }
  
  .card article {
    padding: 20px;
    display: flex;
    
    flex: 1;
    justify-content: space-between;
    flex-direction: column;
    
  }
  .card .thumb {
    padding-bottom: 60%;
    background-size: cover;
    background-position: center center;
    background-image: url(https://www.65ymas.com/uploads/s1/26/27/80/bigstock-walls-and-towers-of-the-fortre-320314996.jpeg);
  }
  
  .card p { 
    flex: 1; 
    line-height: 1.4;
  }

  p.public {
    font-size: 15px;
    font-weight: bold;
    color: #000;
    letter-spacing: .05em;
    margin: 2em 0 0 0;
    text-align: right;
    padding-bottom: 1rem;
  }
  
  h1 {
    font-size: 20px;
    margin: 0;
    color: #333;
  }
  
  .card span {
    font-size: 15px;
    font-weight: bold;
    color: #8BC34A;
    letter-spacing: .05em;
    margin: 2em 0 0 0;
    text-align: right;
  }

  button.boton {
    padding: 1.5rem;
    background-color: #8BC34A;
    color: white;
    border: white;
    font-size: medium;
}